import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { SubProduct } from 'src/app/globalmodel/listitem/subProduct';
import { productCardDTO } from '../moduleServices/productCartDto';

@Component({
  selector: 'product-card',
  templateUrl: './product-card.component.html',
  styleUrls: ['./product-card.component.css']
})
export class ProductCardComponent implements OnInit {
@Input('productObj') product;
private selectedAmount:any=null;
private isadded:boolean=false; 
public addedIndex:number=0;
  public discount:string;
  public sellprice:string
constructor(private productdto:productCardDTO) { 
 
  
}

selectchange(amount:any){
  console.log(amount);
  this.sellprice=(parseFloat(amount.price)-(0.01*parseInt(this.discount)*parseFloat(amount.price))).toString();
}

  ngOnInit() {
    console.log(this.product);
    this.discount=this.product.info.priceAndAmount[0].discount;
  this.discount=this.discount.substring(0,(this.discount.length)-1);
    this.selectedAmount= this.product.info.priceAndAmount[0];
   this.sellprice=(parseFloat(this.selectedAmount.price)-(0.01*parseInt(this.discount)*parseFloat(this.selectedAmount.price))).toString();
  }
  checkIfAddedIndexZero(){
    if(this.addedIndex>0){
      this.isadded=true;
    }
    if(this.addedIndex==0){
      this.isadded=false;
    }
  }

  addProduct(){
 
  this.productdto.addToCart(this.product);
  this.addedIndex++;
  this.checkIfAddedIndexZero();
}

deleteProduct(){
  if(this.isadded==true){
 this.productdto.deleteToCart(this.product);
 this.addedIndex--;
  }
  this.checkIfAddedIndexZero();
}
}

