import {Injectable} from '@angular/core'
import { BehaviorSubject } from 'rxjs';

@Injectable()

export class productCardDTO{
    private productdto=new BehaviorSubject({});
    constructor(){
    }
    returncart(){
        return this.productdto;
    }

  addToCart(obj){
    this.productdto.next({'product':obj,'operation':'ADD'});
  }

  deleteToCart(obj){
      this.productdto.next({'product':obj,'operation':'DELETE'})
  }
}
